.input {
  border: none;
  margin: 0 0 26px;
  padding: 0;
  position: relative;
}

.input:last-of-type {
  margin-bottom: 169px;
}

.input__text-field {
  width: 100%;
  color: #1e1e1e;
  border: none;
  border-bottom: 1px solid #3369f3;
  outline: none;
  margin-bottom: 7px;
  padding-bottom: 7px;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
}

.input__label {
  cursor: text;
}

.input__text {
  width: 100%;
  color: #999;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
  transition: font-size .3s, top .3s;
  position: absolute;
  top: 6px;
  left: 0;
}

.input__text_error {
  color: #ff2f2f;
}

.input__text-field:valid + .input__text {
  top: -8px;
}

.input__text-field:focus + .input__text {
  font-size: 9px;
  line-height: 9px;
  top: -12px;
}

.input__helper-text {
  min-height: 8px;
  color: #ff2f2f;
  opacity: 0;
  margin: 0;
  font-size: 8px;
  font-weight: 400;
  line-height: 8px;
}

.input__helper-text_show {
  opacity: 1;
}

.input__text_isValue {
  font-size: 9px;
  line-height: 9px;
  top: -12px !important;
}

